@import url('https://fonts.googleapis.com/css2?family=IBM+Plex+Sans:wght@200&display=swap');
.navbar {
  height: 80px;
  background-image:linear-gradient(black,transparent);

  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.2rem;
  position: sticky;
top: 0;
  z-index: 999;
  
  letter-spacing: 5px;

  
  
}
.dropdown {
  width: 12vw;
  
  position: absolute;
display: flex;
flex-direction: column;
padding-bottom: 2%;



  background-color:rgba(0, 0, 0, 0.493);
  box-shadow: 0px 8px 16px 0px white;

}

.dropdown-content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: absolute;
  background-color: #f9f9f9;
  
  box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
  padding: 12px 16px;
  z-index: 1;
}


@media screen and (max-width: 960px) {
  .hero{
    font-size: clamp(12px, 10vw, 12px) !important;
    margin-top: 10px;
  }
  .swastik-img{
    margin-top: 10px;
  }
}

.navbar-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 80px;
  max-width: 100vw;
}

@media screen and (max-width: 1200px){
  .navbar-container{
    padding-left: 20px;
    padding-right: 20px;
  }
}

.navbar-logo {
white-space: nowrap;
  color: #fff;
  justify-self: start;
  /* margin-left: 20px; */
  cursor: pointer;
  text-decoration: none;
  font-family: 'IBM Plex Sans', sans-serif;
  font-weight: bolder;
  font-size: 2rem;
  display: flex;
  align-items: center;
}

.fa-typo3 {
  margin-left: 0.5rem;
  font-size: 1.8rem;
}

.nav-menu {
  display: grid;
  grid-template-columns: repeat(4, auto);
  grid-gap: 10px;
  list-style: none;
  text-align: center;
  width: 60vw;
  justify-content: end;
  margin-right: 2rem;
}

.nav-item {
  height: 80px;
}

.nav-links {
  color: #fff;
  display: flex;
  align-items: center;
  text-decoration: none;
  padding: 0.5rem 1rem;
  height: 100%;
}

.nav-links:hover {
  border-bottom: 4px solid #fff;
  transition: all 0.2s ease-out;
}

.fa-bars {
  color: #fff;
}

.nav-links-mobile {
  display: none;
}

.menu-icon {
  display: none;
}




.hero {
  font-size: clamp(30px, 10vw, 30px);
  line-height: 1;
  display: inline-block;
  color: #fff;
  z-index: 2;
  letter-spacing: 10px;

  /* Bright things in dark environments usually cast that light, giving off a glow */
  filter: drop-shadow(0 1px 3px);
}

.hero-nav {
  font-size: clamp(20px, 10vw, 20px);
  line-height: 1;
  display: inline-block;
  color: #ffffff;
  z-index: 2;
  letter-spacing: 10px;

  /* Bright things in dark environments usually cast that light, giving off a glow */
  filter: drop-shadow(0 1px 3px);
}

.layers {
  position: relative;
}
.layers::before,
.layers::after {
  content: attr(data-text);
  position: absolute;
  width: 110%;
  z-index: -1;
}

.layers::before {
  top: 5px;
  left: 5px;
  color: #e0287d;
}

.layers::after {
  top: 2px;
  left: -2px;
  color: #1bc7fb;
}

