	width: calc(100% - 32px);
	padding: 16px;
	border-radius: 40px;
	margin-bottom: 32px;
	position: relative;
	color: white;
	border: 3px solid #b3501896;
	
	filter: drop-shadow(7px 9px 5px rgba(0, 0, 0, 0.04));
	-webkit-filter: drop-shadow(7px 9px 5px rgba(0, 0, 0, 0.04));
	-moz-filter: drop-shadow(7px 9px 5px rgba(0, 0, 0, 0.04));
}

.details-comp > p {
	padding: 2%;
	font-family: 'Akira Expanded', sans-serif;
    font-size: 1em;
}

.summary-colapse {
	font-size: 1.2em;
	font-family: 'Akira Expanded', sans-serif;
	text-transform: uppercase;
	list-style: none;
	border-radius: 8px;
	padding-left: 16px;
	color: var(--ternary);
	font-weight: bold;
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	align-items: center;
	width: 100%;
}
.summary-colapse::-webkit-details-marker {
	display: none;
}

.details-comp[open] > .summary-colapse::after {
	display: flex;
	justify-content: space-between;
	content: url("https://img.icons8.com/office/24/000000/chevron-up.png");
	font-size: 16px;
	align-items: center;
	padding-right: 24px;
}

.details-comp > .summary-colapse::after {
	display: flex;
	justify-content: space-between;
	align-items: center;
	content: url("https://img.icons8.com/office/24/000000/chevron-down.png");
	font-size: 16px;
	padding-right: 24px;
}

.details-comp[open] p {
	-webkit-animation: details-show 350ms linear;
	        animation: details-show 350ms linear;
}

.details-comp:not([open]) p {
	opacity: 0;
}

@-webkit-keyframes details-show {
	0% {
		opacity: 0;
		-webkit-transform: translatey(-25px);
		        transform: translatey(-25px);
	}
	100% {
		opacity: 1;
		-webkit-transform: translatey(0);
		        transform: translatey(0);
	}
}

@keyframes details-show {
	0% {
		opacity: 0;
		-webkit-transform: translatey(-25px);
		        transform: translatey(-25px);
	}
	100% {
		opacity: 1;
		-webkit-transform: translatey(0);
		        transform: translatey(0);
	}
}


.con {
	font-family: 'Akira Expanded', sans-serif;
	font-size: 8rem;
	margin-left: 0%;
	font-weight: 900;
	color: white;
	
	--x-offset: -0.0625em;
	--y-offset: 0.0625em;
	--stroke: 0.025em;
	--background-color: rgba(0, 0, 0, 0.651);
	--stroke-color: #8d837e86;
	
	text-shadow: 
	  
	  var(--x-offset)
	  var(--y-offset)
	  0px
	  var(--background-color), 
	  
	  calc( var(--x-offset) - var(--stroke) )
	  calc( var(--y-offset) + var(--stroke) )
	  0px
	  var(--stroke-color);
	
  }
  /* for browsers that support spread, added in
  https://drafts.csswg.org/css-text-decor-4/#text-shadow-property
  currently: none browsers! none of them!
  I can't even find a test: 
  https://wpt.fyi/results/css/css-text-decor */
  @supports ( text-shadow: 1px 1px 1px 1px black ) {
	.con {
	  text-shadow:
		
		var(--x-offset)
		var(--y-offset)
		0px
		0px
		var(--background-color), 
		
		var(--x-offset) 
		var(--y-offset)
		var(--stroke)
		0px
		var(--stroke-color);
	  
	}
  }
  @media screen and (max-width: 1200px) and (min-width: 300px)  {

	.con{
		font-size: 3rem;
	}
  }
  
