
.slide {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 40%;
  height: 50%;
 
}
:root {
	 --color-text: #fff;
	 --color-bg: #000;
	 --color-link: #f9d77e;
	 --color-link-hover: #fff;
	 --color-info: #efc453;
	 --glitch-width: 100vw;
	 --glitch-height: 100vh;
	 --gap-horizontal: 10px;
	 --gap-vertical: 5px;
	 --time-anim: 4s;
	 --delay-anim: 2s;
	 --blend-mode-1: none;
	 --blend-mode-2: none;
	 --blend-mode-3: none;
	 --blend-mode-4: none;
	 --blend-mode-5: overlay;
	 --blend-color-1: rgba(128, 0, 128, 0.5);
	 --blend-color-2: red;
	 --blend-color-3: rgba(0, 0, 255, 0.103);
	 --blend-color-4: rgba(238, 130, 238, 0.178);
	 --blend-color-5: rgba(0, 132, 255, 0.425);
}


 .glitch2 {
	 width: 50%;
	 height: 100%;
	 position: absolute;
	 overflow: hidden;
}
 .glitch2 .glitch__item {
	display: flex;
	transform: scale(1.4);
	justify-content: center;
	align-items: center;
	 height: 100%;
	 width: 100%;
	 position: absolute;
}
 .glitch2 .glitch__item:nth-child(n+2) {
	 opacity: 0;
	 animation-duration: var(--time-anim);
	 animation-delay: var(--delay-anim);
	 animation-timing-function: linear;
	 animation-iteration-count: infinite;
}
 .glitch2 .glitch__item:nth-child(2) {
	 background-color: var(--blend-color-2);
	 background-blend-mode: var(--blend-mode-2);
	 animation-name: glitch-anim-2;
}
 .glitch2 .glitch__item:nth-child(2) {
	 background-color: var(--blend-color-3);
	 background-blend-mode: var(--blend-mode-3);
	 animation-name: glitch-anim-3;
}
 .glitch2 .glitch__item:nth-child(4) {
	 background-color: var(--blend-color-4);
	 background-blend-mode: var(--blend-mode-4);
	 animation-name: glitch-anim-4;
}
 .glitch2 .glitch__item:nth-child(5) {
	 background-color: var(--blend-color-5);
	 background-blend-mode: var(--blend-mode-5);
	 animation-name: glitch-anim-flash;
}

@media screen and (max-width: 1200px) and (min-width: 300px)  {
	.slide{
		flex-direction: column;
		width: 100vw;
		height: 100vh;
	}

	.glitch2 {
		width:100%;
		height: 50%;
	}
	.glitch2 .glitch__item {
		transform: scale(0.9);
	}

}
 @keyframes glitch-anim-flash {
	 0%, 5% {
		 opacity: 0.2;
		 transform: translate3d(var(--glitch-horizontal), var(--glitch-height), 0);
	}
	 5.5%, 100% {
		 opacity: 0;
		 transform: translate3d(0, 0, 0);
	}
}
 .glitch2 .glitch__title {
	 font-family: 'Bungee', cursive;
	 font-size: 100px;
	 color: #fff;
	 position: relative;
	 text-transform: uppercase;
}
 .glitch2 .glitch__title:before, .glitch .glitch__title:after {
	 content: 'GLITCH';
	 position: absolute;
	 left: 0;
	 top: 0;
	 width: 100%;
	 height: 100%;
	 overflow: hidden;
}
 .glitch2 .glitch__title:before {
	 left: 2px;
	 text-shadow: -1px 0 #00ffea;
	 clip: rect(24px, 550px, 90px, 0);
	 animation: glitch-anim 2s linear infinite alternate-reverse;
}
 .glitch2 .glitch__title:after {
	 left: -2px;
	 text-shadow: -1px 0 #fe3a7f;
	 clip: rect(85px, 550px, 140px, 0);
	 animation: glitch-anim 2s 1s linear infinite alternate-reverse;
}
